.container {
  padding-bottom: 1.35rem;
  padding-top: 1.35rem;

  background: rgb(216, 74, 221);
  background: linear-gradient(45deg, rgba(216, 74, 221, 1) 0%, rgba(255, 218, 95, 1) 100%);
  border-radius: 2px;

  padding-left: 5rem;
  padding-right: 5rem;

  
  
}
*{color:white;}

body {
  background-color: #7a7979;

}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}